.note-detail-view {
  width: 70%;
  padding: 35px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  border: 1px solid #000;
  background: #fff;
  color: #000;
  border-radius: 15px;
  position: relative;
}

.note-detail__title {
  font-size: 32px;
}

.note-detail__content {
  flex: 0 0 70%;
}

.note-detail__tools {
  flex: 0 0 30%;
}

.note-detail__tools button {
  cursor: pointer;
  width: 50%;
  display: block;
  padding: 15px;
  border: 1px solid black;
  border-radius: 5px;
  margin: 5px auto;
}

.copy__text {
  margin: 0 auto;
  width: 50%;
  color: green;
}

.note-detail__tools button:hover {
  background: rgba(255, 255, 255, 0.2);
}

.note-detail__date {
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.new-text__input {
  margin: 15px 0;
  padding: 15px;
  width: 100%;
}

.new-text__input:focus {
  outline: none;
}

.new-text__submit {
  cursor: pointer;
  padding: 15px;
}

.hide {
  display: none;
}

@media (max-width: 992px) {
  .note-detail-view {
    flex-wrap: wrap-reverse;
  }
  .note-detail__content {
    flex: 0 0 100%;
  }

  .note-detail__tools {
    flex: 0 0 100%;
    display: flex;
    justify-content: space-between;
  }
  .note-detail__tools button {
    flex: 0 0 33%;
  }
}
